<template>
  <div class="map-box">
    <div id="viewDiv" ref="viewDiv"></div>
    <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="$emit('signOut',false)">返回</Button>
  </div>
</template>
<script>
import arcgisPackage from '../../utils/arcgisTools'
import config from '../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, shengjie } from '@/utils/layers'
import Basemapthumbnail from '../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../assets/map_images/Basemapthumbnail1.png'
export default {
  name: 'checkInfoMap',
  data () {
    return {
      map: {}, // 地图实例
      view: {} // 地图视图实例
    }
  },
  computed: {},
  props: {
    seeId: {}
  },
  components: {},
  watch: {
    seeId () {
      this.dataId()
    }
  },
  mounted () {
    // 加载地图
    this.createMap()
  },
  methods: {
    dataId () {
      console.log(this.seeId)
    },
    /**
     * @Description:初始化地图的函数
     * @author 张淼
     * @date 2019/5/5
     */
    async createMap () {
      // 加载arcgis地图的依赖
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })
      // 省界
      let layerProvince = await shengjie().then(res => { return res })
      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, layerProvince, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, layerProvince, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })
      this.map = new Map({
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 7
      })
      // 增加底图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap
      })

      this.view.ui.add(basemapToggle, 'top-right')
    },
    /**
     * 添加动态工作空间-解译后的矢量数据
     * @param url
     * @returns {Promise<void>}
     */
    async addShpLayer (shpUrl) {
      let FeatureLayer = await arcgisPackage.FeatureLayer
      const layer = new FeatureLayer({
        url: 'http://10.34.100.135:6080/arcgis/rest/services/anhx/product/MapServer',
        dynamicDataSource: {
          type: 'data-layer',
          dataSource: {
            type: 'join-table',
            leftTableSource: {
              type: 'map-layer',
              mapLayerId: 3
            },
            rightTableSource: {
              type: 'data-layer',
              dataSource: {
                type: 'table',
                workspaceId: 'shp',
                dataSourceName: shpUrl
              }
            },
            leftTableKey: 'STATE_NAME',
            rightTableKey: 'State',
            joinType: 'left-outer-join'
          }
        }
      })
      this.map.add(layer)
      console.log(this.map)
      // layer.on('layerview-create', (event) => {
      //   layer.fullExtent.spatialReference = this.view.spatialReference
      //   this.view.extent = layer.fullExtent.expand(1.4) // 定位图层
      // })
    }
  }
}
</script>
<style scoped lang="scss">
  .map-box{
    width: 100%;
    height: 100%;
    padding: 15px;
    position: relative;
    /* 地图框样式 */
    #viewDiv{
      width: 100%;
      height: 100%;
    }
    .sign-btn {
      position: absolute;
      right: 20px;
      bottom: 30px;
    }
  }
</style>
